.messages{
    padding: 0 15px 0;
    width:1115px;
    background-color: #a9de8c;
}

.message{
    margin-bottom: 10px;
}

.message:last-child{
    margin-bottom: 0;
}

.sendname{

}

.inputarea{
    font-size: 18px;
    padding: 2px 5px;
    border: 1px solid rgba(143, 143, 143, 0.8);
    margin: 10px 0;
    width: 91%;
}

.send__button{
    border: 1px solid rgba(143, 143, 143, 0.8);
    margin: 10px;
    font-family: Monserat, sans-serif;
    font-size: 15px;
    padding: 10px 15px;
    background-color: #25ddde;
}

.send__button:hover{
    background-color: rgba(66,83,218,0.32);
    cursor: pointer;
    transition: background-color .1s;
}

.textmessage{
    font-family: Monserat, sans-serif;
    font-size: 15px;
    color: #282c34;
    padding-left: 5px;
    margin: 5px 0;
}

.link:active, /* активная/посещенная ссылка */
.link:hover,  /* при наведении */
.link {
    text-decoration: none;
    color: #666;
}